import vars from '../globals/variables'

export default class NavigationSearch {
  constructor(el) {
    this.el = el

    // only do this once
    this.uiBlock = document.createElement('div')
    this.uiBlock.classList.add('site-navigation__ui-block', '-search')
    document.body.appendChild(this.uiBlock)

    this.setVariables()
    this.setUp()
    this.addListeners()
  }

  setVariables() {
    this.searchPanel = document.getElementById(
      this.el.getAttribute('aria-controls')
    )
    this.searchInput = this.searchPanel.querySelector('input[type="text"]')
  }

  setUp() {
    this.searchPanel.setAttribute('data-hidden', 'true')
    this.el.setAttribute('aria-expanded', false)
  }

  addListeners() {
    this.el.addEventListener('click', this.toggleSearch.bind(this))
    this.searchPanel.addEventListener(
      'closeSearch',
      function() {
        this.closeSearch('simple')
      }.bind(this)
    )
    this.el.addEventListener(
      'closeMobile',
      function() {
        this.closeSearch('simple')
      }.bind(this)
    )
    this.uiBlock.addEventListener('click', this.closeSearch.bind(this))

    // adding basic support for ESC key to close
    this.searchPanel.addEventListener(
      'keyup',
      this.keyboardCloseSearch.bind(this)
    )
  }

  openSearch() {
    this.uiBlock.setAttribute('data-is', 'open')
    this.el.setAttribute('aria-expanded', 'true')
    this.searchPanel.setAttribute('data-hidden', 'false')
    this.searchInput.focus()
  }

  closeSearch(type) {
    this.uiBlock.setAttribute('data-is', 'closed')
    this.el.setAttribute('aria-expanded', 'false')
    if (type !== 'simple') {
      this.el.focus()
    }
    this.searchPanel.setAttribute('data-hidden', 'true')
  }

  toggleSearch() {
    this.searchPanel.getAttribute('data-hidden') === 'true'
      ? this.openSearch()
      : this.closeSearch()
  }

  keyboardCloseSearch(event) {
    if (event.which === vars.keys.ESC) {
      this.closeSearch()
    }
  }
}



