import whatInput from 'what-input'
import navigationMobile from './globals/navigation-mobile'
import navigationSearch from './globals/navigation-search'
import navigationSubmenu from './globals/navigation-submenu'
import labelFloat from '../../views/modules/label-float/index'

/*
 * custom global event
 */

window.searchMenu = new CustomEvent('closeSearch')
window.mobileMenu = new CustomEvent('closeMobile')

/*
 * init modules
 */

const moduleElements = document.querySelectorAll('[data-module]')

for (let i = 0; i < moduleElements.length; i++) {
  const el = moduleElements[i]
  const name = el.getAttribute('data-module')

  import(`../../views/modules/${name}/index`).then(Module => {
    new Module.default(el)
  })
}

/*
 * init globals
 */

// search menu (desktop only)
const navSearch = document.querySelector('[data-navigation="search"]')
if (navSearch) {
  new navigationSearch(navSearch)
}

// mobile menu toggle
const navMobile = document.querySelector('[data-navigation="mobile"]')
if (navMobile) {
  new navigationMobile(navMobile)
}

// desktop and mobile submenus
const navSubmenu = document.querySelectorAll('[data-navigation="submenu"]')
for (let m = 0; m < navSubmenu.length; m++) {
  new navigationSubmenu(navSubmenu[m])
}

// set selected state of navigation
const navigation = document.getElementById('menu-list')
const navigationPrimarys = navigation.querySelectorAll(
  '.site-navigation__item:not(.-search)'
)

for (let n = 0; n < navigationPrimarys.length; n++) {
  let navigationPrimaryLinks = navigationPrimarys[n].querySelectorAll('a')

  for (let l = 0; l < navigationPrimaryLinks.length; l++) {
    if (navigationPrimaryLinks[l].href === window.location.href) {
      navigationPrimarys[n].classList.add('-current')
    }
  }
}

/*
 * find constant contact signup form and init label float
 */

let ccFormTries = 0

const findCCForm = function() {
  ccFormTries++
  let ccForm = document.querySelector('.ctct-form-custom')

  if (ccForm) {
    new labelFloat(ccForm)
  } else if (ccFormTries <= 15) {
    window.setTimeout(findCCForm, 300)
  }
}

findCCForm()

/*
 * find gravity forms and init label float
 */

const gformField = document.querySelectorAll('.gfield_visibility_visible')
for (let f = 0; f < gformField.length; f++) {
  new labelFloat(gformField[f])
}



