import vars from '../globals/variables'

export default class NavigationMobile {
  constructor(el) {
    this.el = el

    // add this only once
    this.uiBlock = document.createElement('div')
    this.uiBlock.classList.add('site-navigation__ui-block', '-mobile')
    document.body.appendChild(this.uiBlock)

    this.setVariables()
    this.setUp()
    this.addListeners()
  }

  setVariables() {
    this.menu = document.getElementById(this.el.getAttribute('aria-controls'))
    this.subMenus = document.querySelectorAll(
      '[data-navigation="submenu"], [data-navigation="search"]'
    )
  }

  setUp() {
    this.menu.setAttribute('data-hidden', vars.mq.matches ? 'false' : 'true')

    if (!vars.mq.matches) {
      this.uiBlock.setAttribute('data-is', 'closed')
      this.el.setAttribute('aria-expanded', 'false')
    }
  }

  addListeners() {
    vars.mq.addListener(this.setUp.bind(this))
    this.el.addEventListener('click', this.toggleMenu.bind(this))
    this.uiBlock.addEventListener('click', this.closeMenu.bind(this))

    // adding basic support for ESC key to close
    this.menu.addEventListener('keyup', this.keyboardCloseMenu.bind(this))
  }

  openMenu() {
    if (!vars.mq.matches) {
      this.uiBlock.setAttribute('data-is', 'open')
      this.el.setAttribute('aria-expanded', 'true')
      this.menu.setAttribute('data-hidden', 'false')
    }
  }

  closeMenu() {
    if (!vars.mq.matches) {
      this.uiBlock.setAttribute('data-is', 'closed')
      this.el.setAttribute('aria-expanded', 'false')
      this.menu.setAttribute('data-hidden', 'true')

      for (let i = 0, len = this.subMenus.length; i < len; i++) {
        this.subMenus[i].dispatchEvent(window.mobileMenu)
      }
    }
  }

  toggleMenu() {
    this.menu.getAttribute('data-hidden') === 'true'
      ? this.openMenu()
      : this.closeMenu()
  }

  keyboardCloseMenu(event) {
    if (event.which === vars.keys.ESC) {
      this.closeMenu()
      this.el.focus()
    }
  }
}



