module.exports = {
  // media query for large/small versions of navigation
  mq: window.matchMedia('(min-width: 61.25em)'),

  // common keyboard keys used when interacting with the interface
  keys: {
    DOWN: 40,
    ENTER: 13,
    ESC: 27,
    TAB: 9,
    UP: 38
  },

  baseUrl() {
    let baseUrl = 'https://' + window.location.hostname + '/'

    if (window.location.hostname === 'localhost') {
      baseUrl = 'https://feedthefuture.staging.wpengine.com/'
    }

    return baseUrl
  },

  themeUrl: 'wp-content/themes/feedthefuture/'
}



