import vars from '../globals/variables'

export default class NavigationSubmenu {
  constructor(el) {
    this.el = el

    this.setVariables()
    this.setUp()
    this.addListeners()
  }

  setVariables() {
    this.hoverIntent = null
    this.focusoutTimer = null
    this.trigger = this.el.querySelector('[aria-controls]')
    this.menu = document.getElementById(
      this.trigger.getAttribute('aria-controls')
    )
    this.searchMenu = document.getElementById('search')
  }

  setUp() {
    this.menu.setAttribute('data-hidden', 'true')
    this.trigger.setAttribute('aria-expanded', 'false')

    this.uiBlock = document.createElement('div')
    this.uiBlock.classList.add('site-navigation__ui-block', '-menu')
    document.body.appendChild(this.uiBlock)
  }

  addListeners() {
    vars.mq.addListener(this.setUp.bind(this))

    // mouse events
    this.el.addEventListener('mouseenter', this.startHoverIntent.bind(this))
    this.el.addEventListener('mouseleave', this.stopHoverIntent.bind(this))

    // keyboard events
    this.el.addEventListener('keydown', event => {
      if (event.which === vars.keys.UP || event.which === vars.keys.DOWN) {
        event.preventDefault()
      }
    })
    this.el.addEventListener('keyup', this.keyboardToggleMenu.bind(this))
    this.menu.addEventListener('focusout', this.a11yFocusout.bind(this))
    this.menu.addEventListener('focusin', this.a11yFocusin.bind(this))

    // touch events
    if (this.trigger.nodeName === 'BUTTON') {
      this.trigger.addEventListener('click', this.touchToggleMenu.bind(this))
    }

    this.el.addEventListener('closeMobile', this.closeMenu.bind(this))
  }

  startHoverIntent(event) {
    clearTimeout(this.hoverIntent)

    this.hoverIntent = setTimeout(() => {
      this.openMenu(event)
    }, 50)
  }

  stopHoverIntent(event) {
    clearTimeout(this.hoverIntent)
    this.closeMenu(event)
  }

  openMenu(event) {
    if (
      (event.type === 'mouseenter' && vars.mq.matches) ||
      (event.type === 'click' && !vars.mq.matches) ||
      event.type === 'keyup'
    ) {
      this.trigger.setAttribute('aria-expanded', 'true')
      this.menu.setAttribute('data-hidden', 'false')
      this.uiBlock.setAttribute('data-is', 'open')

      if (this.searchMenu && vars.mq.matches) {
        this.searchMenu.dispatchEvent(window.searchMenu)
      }
    }
  }

  closeMenu(event) {
    if (
      (event.type === 'mouseleave' && vars.mq.matches) ||
      (event.type === 'focusout' && vars.mq.matches) ||
      (event.type === 'click' && !vars.mq.matches) ||
      event.type === 'keyup' ||
      event.type === 'closeMobile'
    ) {
      this.uiBlock.setAttribute('data-is', 'closed')
      this.trigger.setAttribute('aria-expanded', 'false')
      this.menu.setAttribute('data-hidden', 'true')
    }
  }

  keyboardToggleMenu(event) {
    if (event.which === vars.keys.DOWN) {
      this.openMenu(event)
    } else if (event.which === vars.keys.UP || event.which === vars.keys.ESC) {
      this.trigger.focus()
      this.closeMenu(event)
    }
  }

  a11yFocusin(event) {
    window.clearTimeout(this.focusoutTimer)
  }

  a11yFocusout(event) {
    this.focusoutTimer = window.setTimeout(() => {
      this.closeMenu(event)
    }, 10)
  }

  // indended for touch, but works for mouse clicks too
  touchToggleMenu(event) {
    this.menu.getAttribute('data-hidden') === 'true'
      ? this.openMenu(event)
      : this.closeMenu(event)
  }
}



