/*
 * LabelFloat: moves overlay input labels out of the way
 */

export default class LabelFloat {
  constructor(el) {
    this.el = el

    this.setVariables()
    this.addListeners()

    // do initial check in case input is already filled in
    this.floatLabel()
  }

  setVariables() {
    this.input = this.el.querySelector('input, textarea')
  }

  addListeners() {
    this.input.addEventListener('input', this.floatLabel.bind(this))
    this.input.addEventListener('focus', this.toggleFocus.bind(this))
    this.input.addEventListener('blur', this.toggleFocus.bind(this))
  }

  floatLabel() {
    this.el.setAttribute('label-is', this.input.value !== '' ? 'floated' : '')
  }

  toggleFocus(event) {
    this.el.setAttribute('input-is', event.type === 'focus' ? 'focused' : '')
  }
}



